import { lichDayData } from "./data"

/**
 * Kiá»ƒm tra xem cÃ³ trÃ¹ng lá»‹ch khÃ´ng
 * @param {string} giangVienId - ID giáº£ng viÃªn
 * @param {string} ngay - NgÃ y dáº¡y (format: YYYY-MM-DD)
 * @param {string} gioBatDau - Giá» báº¯t Ä‘áº§u (format: HH:mm)
 * @param {string} gioKetThuc - Giá» káº¿t thÃºc (format: HH:mm)
 * @param {string} excludeLichId - ID lá»‹ch cáº§n loáº¡i trá»« (khi cáº­p nháº­t)
 * @returns {Object} { isConflict: boolean, conflictingSchedule: Object|null }
 */
export function checkScheduleConflict(giangVienId, ngay, gioBatDau, gioKetThuc, excludeLichId = null) {
  // Chuyá»ƒn Ä‘á»•i giá» sang phÃºt Ä‘á»ƒ so sÃ¡nh
  const timeToMinutes = (time) => {
    const [hours, minutes] = time.split(":").map(Number)
    return hours * 60 + minutes
  }

  const startMinutes = timeToMinutes(gioBatDau)
  const endMinutes = timeToMinutes(gioKetThuc)

  // TÃ¬m lá»‹ch trÃ¹ng
  const conflictingSchedule = lichDayData.find((lich) => {
    // Loáº¡i trá»« lá»‹ch hiá»‡n táº¡i náº¿u Ä‘ang cáº­p nháº­t
    if (excludeLichId && lich.id === excludeLichId) {
      return false
    }

    // Kiá»ƒm tra cÃ¹ng giáº£ng viÃªn vÃ  cÃ¹ng ngÃ y
    if (lich.giangVienId !== giangVienId || lich.ngay !== ngay) {
      return false
    }

    // Kiá»ƒm tra trÃ¹ng thá»i gian
    const lichStartMinutes = timeToMinutes(lich.gioBatDau)
    const lichEndMinutes = timeToMinutes(lich.gioKetThuc)

    // TrÃ¹ng náº¿u cÃ³ khoáº£ng thá»i gian chá»“ng láº¥n
    return (
      (startMinutes >= lichStartMinutes && startMinutes < lichEndMinutes) ||
      (endMinutes > lichStartMinutes && endMinutes <= lichEndMinutes) ||
      (startMinutes <= lichStartMinutes && endMinutes >= lichEndMinutes)
    )
  })

  return {
    isConflict: conflictingSchedule !== undefined,
    conflictingSchedule: conflictingSchedule || null,
  }
}

/**
 * LÆ°u log override vÃ o nháº­t kÃ½
 * @param {Object} logData - Dá»¯ liá»‡u log
 * @param {string} logData.nguoiThucHien - NgÆ°á»i thá»±c hiá»‡n
 * @param {string} logData.lyDo - LÃ½ do override
 * @param {string} logData.monHoc - MÃ´n há»c
 * @param {string} logData.lichCu - ThÃ´ng tin lá»‹ch cÅ© (náº¿u cÃ³)
 * @param {string} logData.lichMoi - ThÃ´ng tin lá»‹ch má»›i
 */
export function saveOverrideLog(logData) {
  // Trong thá»±c táº¿, Ä‘Ã¢y sáº½ gá»i API Ä‘á»ƒ lÆ°u vÃ o database
  // Hiá»‡n táº¡i chá»‰ log ra console
  const logEntry = {
    thoiGian: new Date().toLocaleString("vi-VN"),
    ...logData,
  }

  console.log("Override Log:", logEntry)

  // LÆ°u vÃ o localStorage Ä‘á»ƒ demo (trong thá»±c táº¿ sáº½ lÆ°u vÃ o database)
  const existingLogs = JSON.parse(localStorage.getItem("overrideLogs") || "[]")
  existingLogs.push(logEntry)
  localStorage.setItem("overrideLogs", JSON.stringify(existingLogs))

  return logEntry
}

/**
 * Láº¥y danh sÃ¡ch log override
 * @returns {Array} Danh sÃ¡ch log
 */
export function getOverrideLogs() {
  // Trong thá»±c táº¿, Ä‘Ã¢y sáº½ gá»i API Ä‘á»ƒ láº¥y tá»« database
  const logs = JSON.parse(localStorage.getItem("overrideLogs") || "[]")
  return logs
}

